html, body{
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    color: white;
    user-select: none;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.play_container, .host_container{
    height: 100%;
    width: 100%;
    background-color: #141414;
}

.access_room{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .code_container{
        display: flex;
        flex-direction: column;
        border: 1px solid #434343;
        border-radius: 5px;
        box-sizing: border-box;
        padding: 15px;
        gap: 15px;
        width: 300px;

        #private_code{
            height: 40px;
            text-align: center;
            outline: none;
            border: 1px solid #434343;
            color: white;
            background-color: transparent;
            border-radius: 5px;

            &::-webkit-inner-spin-button, &::-webkit-outer-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
        }

        #submit_private_code{
            height: 40px;
            text-align: center;
            outline: none;
            background-color: #434343;
            border-radius: 5px;
            border: 0;
            color: white;
            cursor: pointer;

            &:hover{
                opacity: 0.8;
            }
        }

    }
}

.waiting_room{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    justify-content: center;
    align-items: center;
    
    .title{
        font-size: 50px;
        font-weight: 600;
    }
}

.quizz_room{
    height: 100%;
    width: 100%;

    .question_container{
        height: 20%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid #434343;
        box-sizing: border-box;
        overflow: hidden;

        .question{
            line-height: 1;
            text-align: center;
            font-size: clamp(1px, 2vw, 250%);
            line-height: 1;
        }
    }

    .answer_container{
        height: 80%;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 10px;
        
        .row{
            display: grid;
            width: 300px;
            height: 30px;
            grid-template-columns: 50px 1fr;
            column-gap: 10px;
            text-align: center;

            .rank{
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #2E2E2E;
                border-radius: 5px;
            }

            .record{
                background-color: #2E2E2E;
                border: none;
                outline: none;
                border-radius: 5px;
                color: white;
                text-align: center;
            }
        }

        .submit_answer{
            background-color: #313131;
            width: fit-content;
            border: none;
            border-radius: 10px;
            color: white;
            padding: 10px 20px;
            margin-top: 20px;
            cursor: pointer;
        }


    }
}

.lobby_room{

    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    
    .start_button{
        position: absolute;
        top: 50px;
        right: 50px;
        background-color: #313131;;
        color: white;
        border: none;
        border-radius: 5px;
        padding: 10px 20px;
        cursor: pointer;

        &:hover{
            opacity: 0.8;
        }
    }

    .invite_code{
        position: relative;
        border: 1px solid #414141;
        border-radius: 5px;
        width: 372px;
        height: 57px;
        appearance: none;
        background-color: #141414;
        color: white;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px 20px;
        font-size: 45px;
        font-weight: 600;
        box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.3);
    }

    .user_connected{
        position: absolute;
        top: 99px;
        display: flex;
        gap: 15px;

        .user{
            width: fit-content;
            padding: 10px 20px;
            font-size: 16px;
            color: white;
            background: #141414;
            border-radius: 5px;
            border: 1px solid #313131;
        }
    }
}

.leaderboard_room{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .table{
        display: flex;
        flex-direction: column;
        gap: 15px;

        .row{
            display: grid;
            width: 425px;
            grid-template-columns: 50px 1fr 75px 75px;
            column-gap: 15px;
            height: 35px;
            text-align: center;

            .rank, .points, .timestamp{
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #2E2E2E;
                border-radius: 5px;
            }
    
            .username{
                background-color: #2E2E2E;
                border-radius: 5px;
                display: flex;
                align-items: center;
                box-sizing: border-box;
                padding: 0 10px;
            }

        }
    }   
}

.review_room{

    height: 100%;
    width: 100%;

    .next_button{
        position: absolute;
        top: 50px;
        right: 50px;
        background-color: #313131;;
        color: white;
        border: none;
        border-radius: 5px;
        padding: 10px 20px;
        cursor: pointer;

        &:hover{
            opacity: 0.8;
        }
    }


    .question_container{
        height: 20%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid #434343;
        box-sizing: border-box;
        overflow: hidden;

        .question{
            line-height: 1;
            text-align: center;
            font-size: clamp(1px, 2vw, 250%);
            line-height: 1;
        }
    }

    .answer_container{
        box-sizing: border-box;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 25px;

        .user_answer{

            display: flex;
            flex-direction: column;
            gap: 10px;
            width: fit-content;
    
            .submit_answer{
                margin-top: 10px;
                padding: 7px 20px;
                color: white;
                background-color: #2E2E2E;
                border-radius: 5px;
                border: none;
                cursor: pointer;

                &:hover{
                    opacity: 0.8;
                }
            }

            .username{
                width: 100%;
                text-align: center;
                font-size: 35px;
                font-weight: 600;
            }
    
            .submit_time{
                width: 100%;
                text-align: center;
                font-weight: 100;
            }
    
            .row{
                width: 450px;
                display: grid;
                grid-template-columns: 50px 1fr 35px 35px;
                column-gap: 10px;
                height: 35px;
        
                .rank{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-color: #2E2E2E;
                    border-radius: 5px;
                }
        
                .record{
                    background-color: #2E2E2E;
                    border-radius: 5px;
                    display: flex;
                    align-items: center;
                    box-sizing: border-box;
                    padding: 0 10px;
                }
        
                .correct, .position{
                    fill: #2E2E2E;
                    height: 100%;
                }
            }
        
        }
    }


}

.login{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #141414;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

    .container{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }

    .title{
        color: white;
        font-size: 30px;
        font-weight: 600;
        margin-bottom: 15px;
    }

    input{
        background-color: #222222;
        height: 40px;
        width: 350px;
        border: none;
        outline: none;
        color: white;
        display: block;
        margin-right: 0;
        padding: 0 10px;
    }

    .validate_login{
        background-color: #222222;
        color: white;
        border: none;
        border-radius: 25px;
        height: 40px;
        width: 350px;
        margin-top: 15px;
        cursor: pointer;

        &:hover{
            opacity: 0.8;
        }
    }
}